@font-face {
  font-family: "Herculanum";
  src: local("Herculanum"), url("/fonts/Herculanum.woff") format("woff"),
    url("/fonts/Herculanum.woff2") format("woff2");
  font-display: swap;
}
.font-herculanum {
  font-family: "Herculanum", "Rajdhani", sans-serif;
}
.hydros-title {
  font-family: "Herculanum", "Rajdhani", sans-serif;
  letter-spacing: 0%;
  font-weight: 400;
  color: rgba(152, 252, 228, 1);
  text-shadow: 0 0 15px rgba(152, 252, 228, 0.5);
  font-size: 40px;
  line-height: 42px;
}
.nav-link {
  position: relative;
  color: rgba(255, 255, 255, 0.9);
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  padding: 0.75rem 1.5rem;
  border-radius: 0.5rem;
  transition: all 0.2s ease;
}
.nav-link:hover {
  color: rgba(152, 252, 228, 1);
}
.nav-link.active {
  color: white;
  backdrop-filter: blur(1px);
  border-bottom: 2px solid rgba(152, 252, 228, 1);
  box-shadow: 0 4px 15px rgba(152, 252, 228, 0.3);
}
.nav-link.active::after {
  content: none;
}
.whitelist-button {
  background: linear-gradient(
    to bottom,
    rgba(255, 200, 0, 1),
    rgba(203, 122, 10, 1)
  );
  border: none;
  color: #000;
  font-family: "Herculanum", "Rajdhani", sans-serif;
  font-size: 1rem;
  font-weight: 600;
  letter-spacing: 0.1em;
  padding: 4px 16px;
  border-radius: 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
  transition: all 0.2s ease;
}
.whitelist-button:hover {
  box-shadow: 0 3px 10px rgba(203, 122, 10, 0.5);
  transform: translateY(-1px);
}
.price-table {
  background: rgba(13, 43, 56, 0.3);
  border: 1px solid rgba(152, 252, 228, 0.3);
  border-radius: 0.75rem;
  backdrop-filter: blur(4px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.25);
}
.countdown-digit {
  font-family: "Herculanum", "Rajdhani", sans-serif;
  font-size: 4rem;
  font-weight: 700;
  color: rgba(152, 252, 228, 1);
  text-shadow: 0 0 15px rgba(152, 252, 228, 0.5);
}
.countdown-label {
  font-family: "Herculanum", "Rajdhani", sans-serif;
  font-size: 1.25rem;
  font-weight: 500;
  color: rgba(255, 255, 255, 0.7);
  margin-top: -0.5rem;
  letter-spacing: 0.05em;
}
